import React, { Component, RefObject } from "react";
import "./your-thing.scss";
import whats from "../../assets/img/whats_txt.png";

interface Props {}
interface State {
  showModel: boolean;
}

export default class YourThing extends Component<Props, State> {
  whatsRef: RefObject<HTMLDivElement> = React.createRef();

  state = { showModel: false };

  componentDidMount() {
    if (window.innerHeight < window.innerWidth) {
      window.addEventListener("mousemove", this._onMouseMove);
    }
  }

  componentWillUnmount() {
    if (window.innerHeight < window.innerWidth) {
      window.removeEventListener("mousemove", this._onMouseMove);
    }
  }

  _onMouseMove = (e: MouseEvent) => {
    var x = e.pageX / (window.innerHeight - 75);
    x = x * -5;
    var y = e.pageY / window.innerWidth;
    y = y * -5;
    if (this.whatsRef.current) {
      this.whatsRef.current.style.transform =
        "translate(" + x + "%, " + y + "%)";
    }
  };

  openVideo = () => {
    this.setState({ showModel: true });
  };

  closeVideo = () => {
    this.setState({ showModel: false });
  };
  render() {
    const { showModel } = this.state;
    return (
      <div className="your-thing">
        <div className="vid-btn" onClick={this.openVideo}></div>
        <div className="whats" ref={this.whatsRef}>
          <img src={whats} alt="What's your thing?" />
        </div>
        {showModel && (
          <div className="model">
            <div className="model-container" onClick={this.closeVideo}>
              <div className="model-content">
                <button title="Close (Esc)" type="button" className="mfp-close">
                  &times;
                </button>
                <iframe
                  title="Godwin"
                  className="mfp-iframe"
                  src="https://www.youtube.com/embed/rCuihDgXqOI?autoplay=1"
                  frameBorder="0"
                  allowFullScreen={false}
                ></iframe>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}
