import { Component } from "react";
import "./header.scss";
import logo from "../../assets/img/logo.png";

interface Props {}
interface State {
  showHeader: boolean;
}

export default class HeaderComponent extends Component<Props, State> {
  state = {
    showHeader: false,
  };

  componentDidMount() {
    window.addEventListener("scroll", this.onScroll);
  }

  onScroll = () => {
    if (window.scrollY >= 150 && !this.state.showHeader) {
      this.setState({ showHeader: true });
    } else if (window.scrollY < 150 && this.state.showHeader) {
      this.setState({ showHeader: false });
    }
  };

  componentWillUnmount() {
    window.removeEventListener("scroll", this.onScroll);
  }

  header = (isFixed: boolean, showHeader: boolean) => (
    <div
      className={`container-fluid header${
        isFixed ? (showHeader ? " fixed-show" : " fixed-hide") : ""
      }`}
    >
      <div className="left pt-3">
        <img src={logo} alt="Little Master" className="p-2" />
      </div>
      <div className="rigth">
        <div className="row" style={{ paddingTop: "35px" }}>
          {/* <div className="col-xs mr-2">
            <button className="btn-signup">
              <a
                href="https://app.littlemaster.com/"
                rel="noreferrer"
                target="_blank"
              >
                Sign up
              </a>
            </button>
          </div> */}
          <div className="col-xs mr-2">
            <button className="btn-login">
              <a
                href="https://app.littlemaster.com/"
                rel="noreferrer"
                target="_blank"
              >
                Login
              </a>
            </button>
          </div>
          <div className="col-xs">
            <button className="btn-subs">
              <a
                href="https://app.littlemaster.com/"
                rel="noreferrer"
                target="_blank"
              >
                Subscribe
              </a>
            </button>
          </div>
        </div>
      </div>
    </div>
  );

  render() {
    const { showHeader } = this.state;
    return (
      <>
        {this.header(false, false)}
        {this.header(true, showHeader)}
      </>
    );
  }
}
