interface FlipCardProps {
  img: string;
  tutor: {
    name: string;
    profession: string;
    description: string;
  };
  backgroundColor: string;
}

interface FlipCardState {}
class FlipCard extends Component<FlipCardProps, FlipCardState> {
  render() {
    const { img, tutor, backgroundColor } = this.props;
    return (
      <div className="flip-card">
        <div className="flip-card-inner">
          <div className="flip-card-front">
            <img src={img} alt={tutor.name} />
          </div>
          <div
            className="flip-card-back"
            style={{ backgroundColor: backgroundColor }}
          >
            <h4>{tutor.name}</h4>
            <p>
              <b>{tutor.profession}</b>
            </p>
            <small>{tutor.description}</small>
          </div>
        </div>
      </div>
    );
  }
}
