export default class Block6 extends Component<Props, State> {
  state = {
    recordingImg: recordedCourse,
    showModel: false,
    modelLink: "",
  };

  _onMouseOver = (course: { img: string; link: string; title: string }) => {
    this.setState({ recordingImg: course.img, modelLink: course.link });
  };
  closeVideo = () => {
    this.setState({ showModel: false });
  };

  render() {
    const { recordingImg, showModel, modelLink } = this.state;
    return (
      <div className="block-6">
        <div className="container p-5">
          <h3>Learn-At-Your-Pace</h3>
          <small>
            A wide - set courses that are self-curated and produced, 'Little
            Master Original Series' are listed to help a learner understand and
            explore what are the basics of a favourite field in a series of
            kid-friendly learn 'At your pace' videos in 2+hrs course format. Go
            ahead, dive into our library and explore what you like.
          </small>
          <div className="video-board mt-3">
            <div className="r1">
              <ul>
                {recordedCoursesList.map((course) => (
                  <li
                    onMouseOver={() => {
                      this._onMouseOver(course);
                    }}
                    onClick={() => {
                      window.open(course.titleLink, "_blank");
                    }}
                  >
                    <p>{course.title}</p>
                  </li>
                ))}
              </ul>
            </div>
            <div
              className="r2"
              style={{ backgroundImage: `url(${recordingImg})` }}
              onClick={() => {
                this._showModel(modelLink);
              }}
            ></div>
          </div>
        </div>
        {showModel && (
          <div className="model">
            <div className="model-container" onClick={this.closeVideo}>
              <div className="model-content">
                <button title="Close (Esc)" type="button" className="mfp-close">
                  &times;
                </button>
                <div
                  style={{
                    paddingTop: "56.25%",
                    position: "relative",
                    overflow: "hidden",
                  }}
                >
                  <iframe
                    title="LittleMaster"
                    frameBorder="0"
                    allowFullScreen={false}
                    scrolling="no"
                    allow="autoplay;fullscreen"
                    src={modelLink}
                    style={{
                      position: "absolute",
                      height: "100%",
                      width: "100%",
                      left: "0px",
                      top: "0px",
                    }}
                  ></iframe>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
  _showModel(modelLink: string) {
    if (modelLink !== "") {
      this.setState({
        modelLink,
        showModel: true,
      });
    }
  }
}
